<template>
  <div>
    <h2>b-progress</h2>
    <hr>
    <br>
    <div class="row py-1">
      <font class="col-1">
        range:
      </font>
      <b-range
        v-model.number="range"
        class="col-11"
      />
    </div>
    <h4>bar(default)</h4>
    <hr>
    <div class="row py-1">
      <font class="col-1">
        default:
      </font>
      <b-progress class="col-2" />
      <font class="col-1">
        value:
      </font>
      <b-progress
        class="col-2"
        :value="range"
      />
      <font class="col-1">
        hideValue:
      </font>
      <b-progress
        class="col-2"
        :value="range"
        hide-value
      />
    </div>
    <div class="row py-1">
      <font class="col-1">
        striped:
      </font>
      <b-progress
        class="col-2"
        :value="range"
        striped
      />
      <font class="col-1">
        animated:
      </font>
      <b-progress
        class="col-2"
        :value="range"
        striped
        animated
      />
      <font class="col-1">
        info:
      </font>
      <b-progress
        class="col-2"
        :value="range"
        info="info text"
      />
    </div>
    <div class="row py-1">
      <font class="col-1">
        size(sm):
      </font>
      <b-progress
        class="col-2"
        :value="range"
        size="sm"
      />
      <font class="col-1">
        size(default):
      </font>
      <b-progress
        class="col-2"
        :value="range"
      />
      <font class="col-1">
        size(lg):
      </font>
      <b-progress
        class="col-2"
        :value="range"
        size="lg"
      />
    </div>
    <div class="row py-1">
      <font class="col-1">
        state:
      </font>
      <font class="col-1">
        false(default):
      </font>
      <b-progress
        class="col-4"
        :color="colorArr1"
        :value="range"
      />
      <font class="col-1">
        true:
      </font>
      <b-progress
        class="col-4"
        :color="colorArr2"
        :value="range"
        state
      />
    </div>
    <br>
    <h4>circle</h4>
    <hr>
    <div class="row py-1">
      <font class="col-1">
        default:
      </font>
      <b-progress
        class="col-2"
        circle
      />
      <font class="col-1">
        value:
      </font>
      <b-progress
        class="col-2"
        circle
        :value="range"
      />
      <font class="col-1">
        hideValue:
      </font>
      <b-progress
        class="col-2"
        circle
        :value="range"
        hide-value
      />
    </div>
    <div class="row py-1">
      <font class="col-1">
        info:
      </font>
      <b-progress
        class="col-2"
        circle
        :value="range"
        info="info text"
      />
      <font class="col-1">
        src:
      </font>
      <b-progress
        class="col-2"
        circle
        :value="range"
        hide-value
        :src="src"
      />
    </div>
    <div class="row py-1">
      <font class="col-1">
        size(sm):
      </font>
      <b-progress
        class="col-2"
        circle
        :value="range"
        info="info text"
        size="sm"
      />
      <font class="col-1">
        size(default):
      </font>
      <b-progress
        class="col-2"
        circle
        :value="range"
        info="info text"
      />
      <font class="col-1">
        size(lg):
      </font>
      <b-progress
        class="col-2"
        circle
        :value="range"
        info="info text"
        size="lg"
      />
    </div>
    <div class="row py-1">
      <font class="col-1">
        state:
      </font>
      <font class="col-2" />
      <font class="col-1">
        false(default):
      </font>
      <b-progress
        class="col-2"
        circle
        :color="colorArr1"
        :value="range"
      />
      <font class="col-1">
        true:
      </font>
      <b-progress
        class="col-2"
        circle
        :color="colorArr2"
        state
        :value="range"
      />
    </div>
    <br>
    <br>
    <h4> state</h4>
    <hr>
    <pre>
      1.  color  
      2.  color  { value: value0-100, color: colorstring  } state 
        1.  state  falsedefault  colorArr1value 
        2.  state  true  colorArr2 value 
      <code>
      colorArr1: [
        {value: 10, color: 'info'},
        {value: 20, color: 'danger'},
        {value: 20, color: 'warning'},
        {value: 30, color: 'secondary'},
        {value: 19, color: 'primary'},
        {value: 1, color: 'success'},
      ],
      1. value  0-100 
      2. value  100 100 primary
      </code>
      <code>
      colorArr2: [
        {value: 10, color: 'info'},
        {value: 30, color: 'danger'},
        {value: 50, color: 'warning'},
        {value: 80, color: 'secondary'},
        {value: 99, color: 'primary'},
        {value: 100, color: 'success'},
      ],
      1. value  value 
      2. value  0-100 primary
      </code>
    </pre>
    <h4>color</h4>
    <hr>
    <div class="row py-1">
      <font class="col-1">
        color:
      </font>
      <div class="col-11">
        <template v-for="item in colors">
          {{ item || '(default)' }}:
          <b-progress
            :key="item"
            class="py-1"
            :color="item"
            value="100"
            show-value
          />
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import variables from "@/components/util/variables.js";

import img from '@/assets/logo.png'
import BProgress from "@/components/base/Progress/b-progress.vue";
import BRange from '@/components/form/b-range.vue'

export default {
  name: "BProgressDemo",
  components: { BProgress, BRange },
  data() {
    return {
      src: img,
      range: 80,
      colors: variables.base.color.bg,
      colorArr1: [
        {value: 10, color: 'info'},
        {value: 20, color: 'danger'},
        {value: 20, color: 'warning'},
        {value: 30, color: 'secondary'},
        {value: 19, color: 'primary'},
        {value: 1, color: 'success'},
      ],
      colorArr2: [
        {value: 10, color: 'info'},
        {value: 30, color: 'danger'},
        {value: 50, color: 'warning'},
        {value: 80, color: 'secondary'},
        {value: 99, color: 'primary'},
        {value: 100, color: 'success'},
      ],
      value: 80,
    };
  },
};
</script>
